<template>
  <div id="tabletRegister">
    <img src="../../assets/circlesMark_images/rllogo.png" alt="">
    <ul>
      <li></li>
      <li>{{data.placeName}}</li>
      <li>{{data.jzNumber}}</li>
      <li></li>
      <li></li>
    </ul>
    <button></button>
  </div>
</template>

<script>
import 'lib-flexible'
import { getMarkerByCoordinate } from '@/api/acquisitionTable'
import { renderTime } from '@/utils/common'
export default {
  name: 'TabletRegister',
  data () {
    return {
      /*  */
      data: {
        lmMarkerRelationPositions: [],
        lmMarkerPhotos: [],
        lon: '',
        lat: ''
      }
    }
  },
  computed: {
    /*  */
    lmPhotos: function () {
      let photosData = [{}, {}, {}, {}, {}, {}]
      for (let i in photosData) {
        for (let j in this.data.lmMarkerPhotos) {
          if (this.data.lmMarkerPhotos[j].type === i * 1 + 1) {
            photosData[i] = this.data.lmMarkerPhotos[j]
          }
        }
        if (photosData[i] === '') {
          photosData[i].url = ''
        }
      }
      let arr1 = [photosData[5]]
      photosData = arr1.concat(photosData)
      photosData.pop()
      return photosData
    }
  },
  props: {
    tabletId: {}
  },
  watch: {
  },
  created () {
    this.getLonLat()
  },
  mounted () {
  },
  methods: {
    /*  */
    getLonLat () {
      if (this.$route.query.lon === undefined || this.$route.query.lat === undefined) {
        this.getLocation()
      } else {
        this.lon = this.$route.query.lon
        this.lat = this.$route.query.lat
        this.getTableData()
      }
    },
    /* http://localhost:8080/#/tabletMove?lon=38.1776&lat=38.1776 */
    /*   38.1776,38.1776 */
    getTableData () {
      getMarkerByCoordinate({
        data: {
          longitude: this.lon * 1,
          latitude: this.lat * 1
        }
      }).then((res) => {
        this.data = res.data.data
      }).catch(() => {
        this.$Message.error('')
      })
    },
    /*  */
    lmMPositionsDate (lmMarkerRelationPositions) {
      if (lmMarkerRelationPositions.length < 3) {
        return lmMarkerRelationPositions.push({})
      } else {
        return lmMarkerRelationPositions.slice(0, 3)
      }
    },
    /*  */
    renderTime (date) {
      return renderTime(date)
    },
    /* 123456 */
    changeImgType (type) {
      switch (type) {
        case 1:
          return ''
        case 2:
          return ''
        case 3:
          return ''
        case 4:
          return ''
        case 5:
          return ''
        case 6:
          return ''
        default:
          return ''
      }
    },
    getLocation () { //   getCurrentPosition() 
      if (navigator.geolocation) { // getCurrentPosition() //showPositioncoordinates
        navigator.geolocation.getCurrentPosition((position) => {
          this.lon = position.coords.longitude
          this.lat = position.coords.latitude
          this.getTableData()
        }, error => {
          switch (JSON.stringify(error.code)) {
            case 1:
              this.$Message.error('')
              break
            case 2:
              this.$Message.error('')
              break
            case 3:
              this.$Message.error('')
              break
            default:
              this.$Message.error('')
          }
        })
      } else {
        this.$Message.error('')
      }
    },
    getwixinLocation () {
      let wx = require('weixin-js-sdk')
      wx.config({
        debug: true, // ,apialertpclogpc
        appId: '', // 
        timestamp: '', // 
        nonceStr: '', // 
        signature: '', // 
        jsApiList: [] // JS
      })
      wx.getLocation({
        type: 'wgs84',
        success: (res) => {
          this.lat = res.latitude
          this.lon = res.longitude
          this.getTableData()
        },
        error: (error) => {
          this.$Message.error(JSON.stringify(error))
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
#tabletRegister{
  width: 100%;
  height: 100%;
  background: url("../../assets/circlesMark_images/tableMove_back.jpg") no-repeat;
  background-size: 100% 100%;
  text-align: center;
  img{
    width: 5.5rem;
    margin-top: 1.95rem;
  }
  ul{
    margin-top: 1.62rem;
    color:#fff;
    li{
      font-size: 0.28rem;
      line-height: 0.54rem;
    }
  }
  button{
    background: url("../../assets/circlesMark_images/tableMove_btn.png");
    background-size: 100% 100%;
    border: 0;
    padding: 0.19rem 0.43rem;
    color:#fff;
    font-size: 0.28rem;
    font-weight: bolder;
    outline: none;
    margin-top: 0.86rem;
    cursor: pointer;
    box-shadow: 0 0.1rem 4px 0px rgba(0, 0, 0, 0.1);
  }
}
</style>
